    <div class="info-wrapper" v-for="f in formacao" :key="f.id">
      <h1 class="formacao-title">{{f.title}}</h1>
      <div class="info">
        <div class="tabs">
          <div class="tab t0">
            <div class="txt">
              Sobre a
              <span class="formacao">formação</span>
            </div>
          </div>
          <div class="tab t1">
            <div class="txt">Porquê?</div>
          </div>
          <div class="tab t2">
            <div class="txt">Inscrições</div>
          </div>
        </div>
        <div class="topics">
          <div class="topic t0" v-html="f.about"></div>
          <div class="topic t1" v-html="f.why"></div>
          <div class="topic t2">
            <div class="input">
              <div class="form-controller">
                <label for="name">Nome completo*</label>
                <input id="name" type="text" />
              </div>
            </div>
            <div class="input">
              <div class="form-controller">
                <label for="email">E-mail*</label>
                <input id="email" type="text" />
              </div>
            </div>
            <div class="input">
              <div class="form-controller">
                <label for="dob">Data de nascimento*</label>
                <input id="dob" type="text" />
              </div>
            </div>
            <div class="input">
              <div class="form-controller">
                <label for="city">Localidade*</label>
                <input id="city" type="text" />
              </div>
            </div>
            <!-- <div class="input">
              <div class="form-controller">
                <label>Já fez algum workshop/masterclass online connosco?*</label>
                <div class="radios">
                  <div>
                    <input id="yes" name="option" type="radio" />
                    <label for="yes">Sim</label>
                  </div>
                  <div>
                    <input id="no" name="option" type="radio" />
                    <label for="no">Não</label>
                  </div>
                </div>
              </div>
            </div>-->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import img from "../../../assets/cadeira.png";
import axios from "axios";

export default {
  data() {
    return {
      img: img,
      title: "",
      formacao: [
        {
          title: "",
          about: "",
          why: "",
        },
      ],
    };
  },
  metaInfo() {
    return {
      title: this.title,
      meta: [
        {
          name: "description",
          content: "Ana Carolina Pereira - Clínica de Psicologia",
        },
        { name: "robots", content: "index,follow" },
        { property: "og:site_name", content: "Ana Carolina Pereira" },
        { property: "og:type", content: "website" },
        {
          property: "og:title",
          content: "",
        },
        {
          property: "og:description",
          content: "Ana Carolina Pereira - Clínica de Psicologia",
        },
      ],
    };
  },
  mounted() {
    var vm = this;

    var topic0 = document.querySelector(".topic.t0");
    var topic1 = document.querySelector(".topic.t1");
    var topic2 = document.querySelector(".topic.t2");

    var tab0 = $(".tab.t0");
    var tab1 = $(".tab.t1");
    var tab2 = $(".tab.t2");

    tab0.click(function () {
      $(".topic.t0").animate({ opacity: "1" }, 500);
      $(".topic.t1").animate({ opacity: "0" }, 500);
      $(".topic.t2").animate({ opacity: "0" }, 500);
    });

    tab1.click(function () {
      $(".topic.t0").animate({ opacity: "0" }, 500);
      $(".topic.t1").animate({ opacity: "1" }, 500);
      $(".topic.t2").animate({ opacity: "0" }, 500);
    });

    tab2.click(function () {
      $(".topic.t0").animate({ opacity: "0" }, 500);
      $(".topic.t1").animate({ opacity: "0" }, 500);
      $(".topic.t2").animate({ opacity: "1" }, 500);
    });

    var location = window.location.href;
    var split_url = location.split("/");
    var ref = split_url[split_url.length - 1];

    var map = Array.prototype.map;

    axios
      .get("https://www.anacarolinapereira.pt/db-api/getFormacaoInfo.php")
      .then((resp) => {
        const data = resp.data;
        var info = [];

        map.call(data, function (d) {
          if (d.code == ref) {
            info.push({
              title: d.title,
              about: d.about,
              why: d.why,
            });

            vm.$data.title =
              d.title + " - Ana Carolina Pereira - Clínica de Psicologia";
            vm.$metaInfo.meta[0].content = "SEO description";
            vm.$metaInfo.meta[4].content =
              d.title + " - Ana Carolina Pereira - Clínica de Psicologia";
            vm.$metaInfo.meta[5].content = "SEO description - Facebook";
          }
        });

        vm.$data.formacao = info;
      });
  },
};
</script>

