.info-wrapper {
  display: flex;
  height: auto;
  padding: 0 pxToEm(130);
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: pxToEm(100);

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    padding: 0 pxToEm(20);
  }

  h1 {
    text-align: center;
    color: $turquiose;
    margin-bottom: pxToEm(50);
  }

  .info {
    max-width: pxToEm(1400);
    width: 100%;
    margin: auto;

    .tabs {
      display: flex;
      width: pxToEm(700);
      justify-content: space-between;
      position: relative;

      @media screen and (max-width: 599px) {
        // flex-direction: column;
        width: 100%;
      }

      @media screen and (max-width: 435px) {
        flex-direction: column;
        width: 100%;
      }

      .tab {
        width: pxToEm(200);
        height: pxToEm(50);
        border-radius: pxToEm(10);
        background-color: $turquiose;
        border: pxToEm(2) solid $turquiose;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: 0.5s;

        @media screen and (max-width: 599px) {
          width: pxToEm(200);
          margin-bottom: pxToEm(20);
        }

        @media screen and (max-width: 435px) {
          width: 100%;
        }

        &:hover {
          background-color: $white;
          border: pxToEm(2) solid $turquiose;
          transition: 0.5s;

          .txt {
            color: $turquiose;
            transition: 0.5s;
          }
        }

        .txt {
          color: #fff;
          text-align: center;
          font-weight: bold;
          transition: 0.5;
        }
      }
    }

    .topics {
      position: relative;
      height: pxToEm(500);
      margin-top: pxToEm(50);

      @media screen and (min-width: 600px) and (max-width: 767px) {
        height: pxToEm(500);
      }

      @media screen and (max-width: 599px) {
        height: pxToEm(700);
      }

      .topic {
        position: absolute;
        opacity: 0;
        font-size: pxToEm(20);
        line-height: 1.5em;
        text-align: justify;

        &.t0 {
          opacity: 1;
        }

        &.t2 {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;

          .input {
            width: pxToEm(500);
            height: pxToEm(90);

            .form-controller {
              width: 100%;
              height: pxToEm(30);
              display: flex;
              flex-direction: column;

              label {
                font-weight: bold;
              }

              input[type="text"] {
                height: 100%;
                border-radius: pxToEm(10);
                border: pxToEm(1) solid #cbcbcb;
              }

              .radios {
                display: flex;
                flex-direction: column;
              }
            }
          }
        }
      }
    }
  }
}
</style>



